import {createStore} from 'redux';
import {combineReducers} from 'redux';
import {golaburl} from "./comm1.js";

const getAjaxData = function (url) {
  return new Promise(function (resolve, reject) {
    fetch(url, {credentials: 'include'})
      .then((response) => response.json())
      .then((data) => resolve(data));
  });
};

const getHeaderData = function () {
  return getAjaxData(golaburl().full + 'src/data/header.json');
};

const getListData = function () {
  return getAjaxData(golaburl().full + 'src/data/lists.json');
};

const getPage2Data = function () {
  return getAjaxData(golaburl().full + 'src/data/page2.json');
};

const gen = function* (cb) {
  return cb();
};

const getHeaderDataState = function (state = [], action) {
  switch (action.type) {
    case 'getHeaderData':
      state = gen(getHeaderData).next();
      return state;
    default:
      return state;
  }
};

const getListDataState = function (state = [], action) {
  switch (action.type) {
    case 'getListData':
      state = gen(getListData).next();
      return state;
    default:
      return state;
  }

};

const getPage2DataState = function (state = [], action) {
  switch (action.type) {
    case 'getPage2Data':
      state = gen(getPage2Data).next();
      return state;
    default:
      return state;
  }
};

const laymState = function (state = [], action) {
  switch (action.type) {
    case 'laym':
      state = action.laym;
      return state;
    default:
      return state;
  }
};

const filterState = function (state = [], action) {
  switch (action.type) {
    case 'filter':
      state = action.filter;
      return state;
    default:
      return state;
  }
};

const counter = combineReducers({
  getHeaderData: getHeaderDataState,
  getListData: getListDataState,
  getPage2Data: getPage2DataState,
  laym: laymState,
  filter: filterState,
});

